body {
  font-family: Georgia, "Times New Roman", Times, serif;
  text-align: center;
}

header {
  text-align: center;
  background: #283b63;
  padding: 1%;
}

header ul li {
  align-self: center;
}

article {
  width: 50%;
  margin: 0 auto;
}

.flex-container {
  display: flex;
  justify-content: space-between;
}

#welcome {
  height: 1200px;
  background-image: url("assets/pexels-josiah-lewis-772689.jpg");
  background-size: cover;
  color: white;
  padding-top: 50px;
  text-shadow: 3px 1px #333;
}

h1 {
  font-size: 40px;
}

ul {
  display: flex;
  justify-content: space-around;
}

ul li {
  list-style: none;
}

ul li a {
  color: white;
  text-decoration: none;
}

table {
  width: 100%;
  text-align: center;
}

th {
  background-color: rgb(184, 184, 184);
}

table,
th,
td {
  border: 1px solid black;
  border-collapse: collapse;
}
